* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  line-height: 12px;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__logo {
  align-items: center;
  height: 60px;
  margin-left: 50px;
  display: flex;
}

.header__logo img {
  height: 40px;
  margin: 0;
}

.nav {
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__list li {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
}

.nav__list li:first-child {
  margin-top: 0;
  margin-left: 0;
}

.nav__list li:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .active_hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 3rem;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
